<template>
  <div class="editor">
    <div class="source">
      <textarea
        wrap="off"
        v-model="mdText"
        @input="emitMd"
      >
      </textarea>
    </div>
    <div class="resize"></div>
    <div class="review">
      <div class="markdown-body" v-html="mdHtml.html"></div>
    </div>
  </div>
</template>

<script>
import "github-markdown-css/github-markdown.css"
import hljs from 'highlight.js'
import 'highlight.js/styles/default.css'
const opts = {
  level: 1,//10
  excerpt: 0,//0
}
const md = require('markdown-it')({
  highlight: function (str, lang) {
    if (lang && hljs.getLanguage(lang)) {
      try {
        return hljs.highlight(lang, str).value;
      } catch (__) {
        console.log(false);
      }
    }

    return ''; // 
  }
}).use(require('markdown-it-title'), opts);//markdown-it

export default {
  props: [
    'md',
  ],

  data() {
    return {
      mdText: '',
    }
  },

  methods: {
    //div
    dragControllerDiv: function() {
      let editor = document.getElementsByClassName('editor')[0]
      let resize = document.getElementsByClassName('resize')[0];
      let leftDiv = document.getElementsByClassName('source')[0];
      resize.onmousedown = function() {
        document.onmousemove = function(e) {
          let moveLen = 0;
          moveLen = e.clientX < 400 ? 400 : e.clientX;//
          moveLen = e.clientX > editor.clientWidth - 400 ? editor.clientWidth - 400 : moveLen;//
          leftDiv.style.flex = `0 0 ${moveLen}px`;
        }

        document.onmouseup = function() {
          document.onmousemove = null;
          document.onmouseup = null;
        }
      }
    },

    //md
    emitMd: function() {
      let md = {
        //mdHtml
        title: this.mdHtml.env.title ? this.mdHtml.env.title + '.md' : this.md.title,
        content: this.mdText
      }
      this.$emit('save', md);
    }
  },

  mounted() {
    //div
    this.dragControllerDiv();

    //md.contentmdText
    this.mdText = this.md.content;

    //localStorage
    localStorage.setItem('mdHtml', this.mdHtml.html);
    localStorage.setItem('mdTitle', this.mdHtml.env.title ? this.mdHtml.env.title + '.md' : this.md.title);
  },

  watch: {
    //md
    md: function() {
      //md.contentmdText
      this.mdText = this.md.content;

      //localStorage
      localStorage.setItem('mdHtml', this.mdHtml.html);
      localStorage.setItem('mdTitle', this.mdHtml.env.title ? this.mdHtml.env.title + '.md' : this.md.title);
    }
  },

  computed: {
    //md
    mdHtml() {
      let env = {};
      let mdHtml = md.render(this.mdText, env);
      return {
        html: mdHtml,//
        env: env,//env.title(env.excerpt[x])
      };
    },
  }
}
</script>

<style>
.editor {
  display: flex;
  flex-direction: row;
}
.source {
  flex: 0 0 50%;
}
.source > textarea {
  padding: 10px;
  width: 100%;
  height: calc(100% - 6px);
  box-sizing: border-box;
  border: none;
  outline: none;
  font-size: 14px;
  line-height: 21px;
  font-family: '';
  resize: none;
  background: #f9f9f9;
}
.resize {
  flex: 0 0 5px;
  background: #ddd;
  user-select: none;
  cursor: col-resize;
}
.resize:hover {
  background: #ccc;
}
.review {
  flex: 1;
  overflow-y: scroll;
}
.review > .markdown-body {
  padding: 20px;
  min-height: 100%;
  box-sizing: border-box;
}
</style>