<template>
  <div class="home">
    <div class="topbar">
      <n-button quaternary size="tiny" @click="newMd()"></n-button>
      <n-button quaternary size="tiny" @click="upLoadMarkdown"></n-button>
      <n-button
        quaternary
        size="tiny"
        @click="downloadAsMarkdown"
        :disabled="!exportAble"
      >
      </n-button>
      <n-button quaternary size="tiny" @click="openFullScreenReview"></n-button>
    </div>
    <div class="main">
      <div class="tabbar">
        <div
          v-for="(md,index) in mdList"
          :key="md"
          :class="'tab'+(index==currentMdIndex?' active':'')"
          @click="selectMd(index)"
        >
          <div>{{ md.title }}</div>
          <n-icon size="18" @click.stop="deleteMd(index)">
            <cancel-filled />
          </n-icon>
        </div>
      </div>
      <editor v-if="hasMd" :md="mdList[currentMdIndex]" @save="getNewMd" />
      <div class="saying" v-else><div></div></div>
    </div>
  </div>
</template>

<script>
import { NButton, NIcon } from 'naive-ui';
import { CancelFilled } from '@vicons/material';
import Editor from '../components/Editor.vue'

export default {
  components: {
    NButton,
    CancelFilled,
    NIcon,
    Editor
  },
  data() {
    return {
      mdList: [//
        {
          title: ' Markdown Editor.md',
          content: `#  Markdown Editor
-  \`\`  \`\` 
-  \`\`  ***markdown***  
-  \`\`  ***pdf*** `
        }
      ],
      currentMdIndex: 0,//
    }
  },

  methods: {
    //index
    selectMd: function(index) {
      this.currentMdIndex = index;
    },

    //
    newMd: function(md) {
      if (!md) {
        md = {
          title: '.md',
          content: '# '
        }
      }
      this.mdList.push(md);
      //
      this.selectMd(this.mdList.length - 1);
    },

    //index
    deleteMd: function(index) {
      this.mdList.splice(index, 1);
      if (index <= this.currentMdIndex && this.currentMdIndex > 0) {//(>0)-1
        this.selectMd(this.currentMdIndex - 1);
      } else if (index == this.currentMdIndex == 0) {//0+1
        this.selectMd(this.currentMdIndex + 1);
      }
    },
    
    //Editor
    getNewMd: function(md) {
      this.mdList[this.currentMdIndex] = md;
    },
    
    //markdown
    upLoadMarkdown: function() {
      let $this = this;
      let link = document.createElement('input');
      link.setAttribute('type', 'file');
      link.setAttribute('accept', '.md,.txt');
      link.style.display = 'none';
      document.body.appendChild(link);
      //change
      link.addEventListener('change', function() {
        if (link.value != '') {
          let file = link.files[0];
          let reader = new FileReader();
          reader.readAsText(file, 'UTF-8');
          reader.onload = function(e) {
            $this.newMd({
              title: file.name,
              content: e.target.result
            });
            document.body.removeChild(link);
            console.log(`${file.name}`);
          }
        }
      });
      link.click();
    },

    //markdown
    downloadAsMarkdown: function() {
      let link = document.createElement('a');
      link.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(this.mdList[this.currentMdIndex].content));
      link.setAttribute('download', this.mdList[this.currentMdIndex].title);
      link.style.display = 'none';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },

    //
    openFullScreenReview: function() {
      let { href } = this.$router.resolve({
        path: '/previewFullScreen'
      });
      window.open(href, '_blank');
    },
  },

  computed: {
    //mdList (editor)
    hasMd: function() {
      return this.mdList.length == 0 ? false : true;
    },

    //
    exportAble: function() {
      //false
      return this.mdList.length > 0;
    }
  },
}
</script>

<style>
.home {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.topbar {
  padding: 0 10px;
  flex: 0 0 30px;
  display: flex;
  justify-content: left;
  align-items: center;
  background: #ccc;
}
.main {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 0;
  background: #fefefe;
}
.tabbar {
  flex: 0 0 36px;
  display: flex;
  flex-direction: row;
  background: #ddd;
  box-shadow: 2px 0 2px #aaa;
  z-index: 999;
}
.tabbar > .tab {
  position: relative;
  width: 130px;
  height: 100%;
  background: #d5d5d5;
  text-align: center;
  line-height: 36px;
  color: #575e55;
  cursor: pointer;
  user-select: none;
}
.tabbar > .tab.active {
  background: #eee;
  color: #83b56f;
}
.tabbar > .tab > div {
  margin-left: 16px;
  width: 100px;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
}
.tabbar > .tab > .n-icon {
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  color: #999;
}
.tabbar > .tab > .n-icon:hover {
  color: #555;
}
.editor {
  flex: 1;
  height: 0;
  background: #f9f9f9;
}
.saying {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #aaa;
}
</style>